.form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 10px;
}

.label {
  grid-column: 1/2;
  align-self: center;
  justify-self: right;
  padding-right: 10px;
}

.search {
  grid-column: 2/3;
  padding: 4px 5px 4px;
  font-family: "MedievalSharp", cursive;
  border-radius: 5px;
  border: 0;

  &.input {
    width: 144px;
    &.gryffindor {
      background-color: #efba30;
      color: #ae0001;
    }

    &.hufflepuff {
      background-color: #ecb939;
      color: #6a5b50;
    }

    &.ravenclaw {
      background-color: #946b2d;
      color: #26335c;
    }

    &.slytherin {
      background-color: #5d5d5d;
      color: #aaaaaa;
    }
  }

  &.select {
    width: 154px;
    &.gryffindor {
      background-color: #efba30;
      color: #ae0001;
    }

    &.hufflepuff {
      background-color: #ecb939;
      color: #6a5b50;
    }

    &.ravenclaw {
      background-color: #946b2d;
      color: #26335c;
    }

    &.slytherin {
      background-color: #5d5d5d;
      color: #aaaaaa;
    }
  }

  &.input::placeholder {
    color: black;
  }
}

.reset {
  font-family: "MedievalSharp", cursive;
  background-color: white;
  width: fit-content;
  grid-column: 1/3;
  justify-self: center;
  padding: 4px;
  border-radius: 5px;
  border: 0;

  &.gryffindor {
    background-color: #efba30;
    color: #ae0001;
  }

  &.hufflepuff {
    background-color: #ecb939;
    color: #6a5b50;
  }

  &.ravenclaw {
    background-color: #946b2d;
    color: #26335c;
  }

  &.slytherin {
    background-color: #5d5d5d;
    color: #aaaaaa;
  }
}

@media (min-width: 1024px) {
  .form {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    font-size: 20px;
    column-gap: 10px;
  }

  .name {
    grid-column: 2/3;
    grid-row: 1/3;
  }

  .house {
    grid-column: 4/5;
  }

  .search {
    font-size: 18px;
    height: fit-content;
    align-self: center;
    &.input {
      grid-column: 3/4;
      width: 240px;
      grid-row: 1/3;
    }

    &.select {
      grid-column: 5/6;
      width: 254px;
    }
  }
  .reset {
    font-size: 18px;
    grid-column: 5/6;
  }
}
